.link {
  display: inline-block;
  font-family: $font;
  font-size: rem(14px);
  font-weight: 400;
  line-height: 1.2;
  color: $primary-color;

  transition: all 0.4s ease;

  &--underline {
    box-shadow: inset 0 -1px 0 #3a4047;
    &:hover,
    &:active,
    &:focus {
      box-shadow: none;
    }
  }
}
